import {mapState} from 'vuex'
export default {
  name: 'pay_success',
  components: {},
  inject: ['reload'], // 引入方法
  data () {
    return {
      orderId: '',
      showMore: false
    }
  },
  computed: {
    ...mapState({
    }),
    noticeTxt: function () {
      return this.showMore ? '收起' : '查看更多'
    }
  },
  watch: {
  },
  mounted () {
    if (this.$route.params.orderId) {
      this.orderId = this.$route.params.orderId
    }
  },
  destroyed () {
  },
  methods: {
  }
}



